const getMonthBetween = ({ endDate, startDate }) => {
  const monthDiff = parseInt(
    (new Date(endDate) - new Date(startDate)) / (60 * 60 * 1000 * 30 * 24)
  );
  return `${
    parseInt(monthDiff / 12) === 0 ? '' : `${parseInt(monthDiff / 12)}년 `
  }${monthDiff % 12 !== 0 ? `${monthDiff % 12}개월` : ''}`;
};

